<template>
  <div class="null">
    <div class="null">
      <div class="header">
        <div class="container">
          <div class="row">
            <div class="header__logo">
              <a href="/"><img src="../../assets/logo.png" alt="logo" /></a>
            </div>
            <div class="header__item">
              <ul>
                <li v-for="(item, index) in menuItems" v-bind:key="index">
                  <router-link :key="index" :to="item.route">{{item.link}}</router-link>
                </li>
                <li v-on:click="signout" v-if="isUserAuthenticated">
                  <router-link to="/">Выйти</router-link>
                </li>
              </ul>
            </div>
            <div class="header__menu">
              <div class="burger-menu" v-on:click="toggleMenu">
                <span class="line"></span>
                <span class="line"></span>
                <span class="line"></span>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="menu" v-bind:class="{show: OpenBurgerMenu}">
        <div class="container">
          <div class="row">
            <div class="menu__item">
              <div class="header__item">
                <ul>
                  <li v-for="(item, index) in menuItems" v-bind:key="index">
                    <router-link :key="index" :to="item.route">{{item.link}}</router-link>
                  </li>
                  <li v-on:click="signout" v-if="isUserAuthenticated">
                    <router-link to="/">Выйти</router-link>
                  </li>
                </ul>
              </div>
            </div>
            <div class="menu__close">
              <span class="close" v-on:click="toggleMenu">&times;</span>
            </div>
          </div>
        </div>
      </div>
    </div>
    <router-view />
  </div>
</template>


<script>
  export default {
    computed: {
      isUserAuthenticated() {
        return this.$store.getters.isUserAuthenticated
      },
      menuItems() {
        return this.isUserAuthenticated ?  [
          {
            id: 1,
            link: 'Книги',
            route: '/books',
          },
          {
            id: 2,
            link: 'Курсы',
            route: '/course'
          },
          {
            id: 3,
            link: 'Мой кабинет',
            route: '/profile'
          },
          ] : [
          {
            id: 4,
            link: 'Главная',
            route: '/'
          },
          {
            id: 5,
            link: 'Книги',
            route: '/books',
          },
          {
            id: 6,
            link: 'Курсы',
            route: '/course'
          },
          {
            id: 7,
            link: 'Войти',
            route: '/signin'
          },
          {
            id: 8,
            link: 'Зарегистрироваться',
            route: '/signup'
          }
        ]
      },
    },
    data() {
      return {
        OpenBurgerMenu: false,
      }
    },
    methods: {
      toggleMenu() {
        this.OpenBurgerMenu = !this.OpenBurgerMenu
      },
      signout() {
        this.$store.dispatch('SIGNOUT')
      },
    },
  }
</script>

<style lang="scss" scoped>
  .header {
    background: #fff;
    &__logo {
      img {
        max-width: 50px;
        transition: .3s;
        &:hover {
          transform: rotate(-180deg);
        }
      }
    }
    &__item {
      ul {
        display: flex;
      }
      li {
        margin: 0 20px;
      }
      a {
        text-decoration: none;
        font-size: 14px;
        color: #000;
        font-weight: 300;
        letter-spacing: 1.5px;
        transition: .3s;
        padding-bottom: 30px;
        &:hover {
          color: #888;
          border-bottom: 1px solid #000;
        }
      }
    }

    &__logo, &__item, &__menu {
      display: flex;
      align-items: center;
      align-self: center;
      height: 80px;
    }
  }

  .burger-menu {
    cursor: pointer;
    .line {
      display: block;
      width: 16px;
      height: 1px;
      background: #000;
      margin: 3px 0;
    }
  }

  .show {
    top: 0!important;
  }

  .menu {
    background: #fff;
    height: 100vh;
    width: 100%;
    position: absolute;
    left: 0;
    right: 0;
    top: -100%;
    bottom: 0;
    transition: .3s;
    &__item, &__close {
      display: flex;
      height: 80px;
      align-items: center;
      align-self: center;
    }
  }

  .close {
    cursor: pointer;
    font-size: 20px;
  }

  .container {
    max-width: 1200px;
    margin: 0 auto;
  }

  .row {
    display: flex;
    justify-content: space-between;
  }

  .unshow {
    display: none;
  }
</style>
